import { Component, OnInit } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { User } from 'src/app/common/dto/user';
import { mustMatch } from 'src/app/common/function/input.validation.function';
import { UserService } from 'src/app/common/service/user/user.service';

@Component({
	selector: 'app-register',
	templateUrl: './register.component.html',
	styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

	public loginForm: FormGroup;

	constructor(builder: FormBuilder, private toastr: ToastrService, private auth: AngularFireAuth,
		private router: Router, private userService: UserService) {
		this.loginForm = builder.group({});

		const email: AbstractControl = new FormControl('', [Validators.email, Validators.required]);
		const password: AbstractControl = new FormControl('', [Validators.required, Validators.minLength(8)]);
		const cpassword: AbstractControl = new FormControl('', [Validators.required, Validators.minLength(8), mustMatch(password)]);

		this.loginForm.addControl("email", email);
		this.loginForm.addControl("password", password);
		this.loginForm.addControl("cpassword", cpassword);

	}

	ngOnInit(): void {
	}

	onSubmit(): void {
		this.loginForm.markAllAsTouched();
		if (this.loginForm.valid) {
			let user: User = this.loginForm.value as User;

			this.userService.checkIfUserExists(user.email).then(
				() => {
					this.auth.createUserWithEmailAndPassword(user.email, user.password).then(
						() => {
							this.toastr.success("Account created! You will be redirected to homepage shortly.");
							this.router.navigateByUrl("/home")
						},
						(reason: any) => {
							this.toastr.error("Could not create account due to '" + reason.message + "'.");
						}
					)
				},
				() => {
					this.toastr.error("We could not find your account with us please register at https://www.syncupai.com.");
				}
			)
		}
	}

}
